@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;

    font-family: 'Montserrat', serif;

    background-image: url(/static/media/background.4d2aac39.png);
    background-color: #1D1D1D;
    background-size: cover;
    background-attachment: fixed;
    background-position: center bottom;
}

#root {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
}

/* Layout */
#page-wrapper {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;
}

#options-wrapper {
    width: 100%;
}

.stackable {
    margin-bottom: 15px;
    display: inline-block;
}

/* Text Formatting */
.name-title {
    font-size: 15vmin;
    /*font-size: 150px;*/
    text-align: center;

    margin: 0 0 10px;

    color: #FFFDFD;
}

.role-title {
    /*font-size: 45px;*/
    font-size: 4.5vmin;
    text-align: center;

    margin: 0 0 10px;

    color: #2ac9d4;
}

.backed-text {
    margin: 10px;
    padding: 0 15px;
    background-color: rgba(29, 29, 29, 0.5);
}

.option-text {
    color: white;
    /*font-size: 40px;*/
    font-size: 4.0vmin;
    text-decoration: none;
}

.option-text:hover {
    text-decoration: overline;
}

/* Small screen options */
@media only screen and (max-width: 800px) {
    .name-title {
        font-size: 60px;
    }

    .role-title {
        font-size: 30px;
    }

    .option-text {
        font-size: 30px;
    }
}
